export default {
  namespace: 'demo',
  /**
   * StateModeljavascript
   * immutable data
   * State
   * 
   *   state  reducer 
   *  {
   *    a: {
   *      b: {
   *        c: 1
   *      }
   *    }
   *  }
   */
  state: {
    value: 'hello lucio'
  },
  /**
   * Subscriptionselm ( http://elm-lang.org/blog/farewell-to-frp )
   * Subscription()dispatchactio
   * websocketkeyboardhistory
   */
  subscriptions: {
    setup({ dispatch, history }) {
      history.listen(({ pathname }) => {
        if (pathname === '/') {
          dispatch({ type: 'effectsreducers' });
        }
      });
    }
  },
  /**
   * Effect()
   * 
   * dvaredux-sagasgeneratoreffects
   * put  action 
   * select  state  const user = yield select(state => state.user);
   * call  
   */
  effects: {
    /**
     * @param {*} action 
     * @param {*} redux-saga {select, put, call}
     * * add({}, {select, put, call}) {
     *   yield put({type: 'reducers'})
     *   const demo = yield select(state => state.demo);
     *   const { status, data } = yield call(http);
     * } 
     */
  },
  /**
   * type Reducer<S, A> = (state: S, action: A) => S
   * Reducer 
   *   1.modelstate <-> 
   *   2. <-> 
   * 
   * 
   *  1.Reducer 
   *  2.immutable data
   */
  reducers: {
    /**
     * @param {*} state 
     * @param {*} action 
     * add(state, {value}) {
     *   return {...state, value}
     * } 
     */
  }
};


